
.container{
  background: #ececec;
  box-sizing: border-box;
  padding: 20px; 
  margin: 0 10px;
  display: flex;
  flex-flow: wrap;
  border-radius: 3px;
  
}

.container > div{
  flex: 1;
  /* border: 1px solid #000; */
  min-width: 350px;
}

.container > div:nth-child(3){
  min-width: 650px;
}


@media (max-width:700px) {
  .container div:nth-child(3){
    min-width: 350px;
  }
}

@media (max-width:420px) {
  .container > div{
    flex: 1;
    min-width: 100%;
  }

  .container > div:nth-child(3){
    min-width: 100%;
  }
}